<template>
	<view class="">
		<view class="area-search">
			<input @input="cityInput" class="input" type="text" v-model="address" placeholder="请输入城市"
				placeholder-style="color:#000" />
		</view>
		<scroll-view scroll-y="true" v-if="isShow_cityScroll" class="scroll">
			<view class="scrollItem" v-for="(item,index) in citySearchList" :key="index" @click="citySelect(item)">
				{{item.adm1+' - '+item.adm2+' - '+item.name}}
			</view>
		</scroll-view>
		<view class="city-container">
			<city-weather :city="selectCity"> </city-weather>
		</view>
	</view>

</template>

<script>
	import cityWeather from '@/pages/cityWeather/cityWeather.vue'
	import {
		city_search
	} from "@/weather-http/index.js"
	import {
		mapState
	} from 'vuex';

	export default {
		components: {
			cityWeather
		},
		data() {
			return {
				address: "",
				isShow_cityScroll: false,
				citySearchList: []
			};
		},
		onLoad() {
			console.log("index onload");
		},
		computed: {
			...mapState({
				citys: state => state.citys.cityList,
				selectCity: state => state.citys.citySelect
			})
		},
		methods: {
			cityInput(e) {
				if (this.address) {
					this.isShow_cityScroll = true
					city_search(this.address).then((res) => {
						this.citySearchList = res.data.location
					});
				} else {
					this.isShow_cityScroll = false
				}
			},
			citySelect(city) {
				this.$store.commit('selectCity', city);
				this.address = ''
				this.isShow_cityScroll = false
			}
		}
	}
</script>

<style lang="less">
	.city-container {
		overflow: auto;
		display: flex;
		flex-direction: row;
		// width: 100%;
		// justify-content: space-evenly;
		// align-items: flex-start;

	}

	.area-search {
		display: flex;
		height: 100rpx;
		align-items: center;
		justify-content: space-around;

		margin: 3%;

		.input {
			outline: 0;
			padding: 5px;
			color: #333;
			font-size: 18px;
			width: 90%;
			border: 0.5px solid #d9d9d9;
			border-radius: 10rpx;

		}
	}

	.scroll {
		position: absolute;
		z-index: 999;
		left: 0;
		right: 0;

		margin: auto;
		width: 100%;
		height: 500rpx;
		background: rgba(255, 255, 255, 0.81);

		.scrollItem {
			color: #333;
			padding: 30rpx 20rpx;
			border-bottom: 2rpx solid #e6e6e6;
		}
	}
</style>